import React from 'react';
import DateFnsUtils from '@date-io/date-fns';

import TimeChart from '../../component/TimeChart'
import Title from './Title';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';

import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Box from '@material-ui/core/Box';
import RemoveIcon from '@material-ui/icons/Remove';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}
const useStyles = makeStyles(theme => ({
  timeBar: {
    display:'flex',
    backgroundColor: theme.palette.background.paper,
    width: ' 100%',
    margin:'20px 0',
    justifyContent: 'center',
    alignItems:'center'
  },
  mt:{
    marginTop:26,
  }

}));

export default function Chart() {

  const [selectedDate, setSelectedDate] = React.useState();
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const [ivalue, setIValue] = React.useState(0);

  const ihandleChange = (event, newValue) => {
    setIValue(newValue);
  };

  const handleChange = (event, newValue) => {

    setValue(newValue);
  };

  const handleChangeIndex = index => {
    setValue(index);
  };
  const handleDateChange = date => {
    setSelectedDate(date);
  };
  return (
    <React.Fragment>
      <Title>测量项目具体历史数据</Title>
      <Paper className={classes.root}>
        <Tabs
          value={ivalue}
          onChange={ihandleChange}
          indicatorColor="primary"
          textColor="primary"
          centered
        >
          <Tab label="血压" />
          <Tab label="血糖" />
          <Tab label="心率" />
          <Tab label="血氧" />
          <Tab label="体脂" />
          <Tab label="体重" />
        </Tabs>
      </Paper>
      <div className={classes.timeBar}>

        <MuiPickersUtilsProvider utils={DateFnsUtils}>

          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            format="MM/dd/yyyy"
            margin="normal"
            id="date-picker-inline"
            label="Date picker inline"
            value={selectedDate}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change date',
            }}
          />
          <RemoveIcon className={classes.mt} />
          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            format="MM/dd/yyyy"
            margin="normal"
            id="date-picker-inline"
            label="Date picker inline"
            value={selectedDate}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change date',
            }}
          />
        </MuiPickersUtilsProvider>
        <ButtonGroup size="small" aria-label="outlined primary button group" className={classes.mt}>
          <Button>七天</Button>
          <Button>一个月</Button>
          <Button>三个月</Button>
          <Button>半年</Button>
          <Button>一年</Button>
          <Button>全部</Button>
        </ButtonGroup>
&nbsp;&nbsp;
<Button size="small" variant="contained" color="primary" className={classes.mt} disableElevation>
          查看
</Button>
      </div>
      <TimeChart />




    </React.Fragment>
  );
}