import React from 'react';

import { makeStyles } from '@material-ui/core/styles';

import Title from './Title';
import TodayTestBox from '../../component/TodayTestBox'
import bloodPressure from './svg/bloodPressure.svg';
import bloodSugar from './svg/bloodSugar.svg';
import heartRate from './svg/heartRate.svg';
import heartOxygen from './svg/heartOxygen.svg';
import bodyFat from './svg/bodyFat.svg';
import temperature from './svg/temperature.svg';






const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    justifyContent: 'space-around',
    marginTop: 20,
  }
}));

export default function Orders() {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Title>今日测试项目统计</Title>
      <div className={classes.container}>
        <TodayTestBox svg={(<img src={bloodPressure} alt="bloodPressure" />)} title='血压' count='1' />
        <TodayTestBox svg={(<img src={bloodSugar} alt="bloodSugar" />)} title='血糖' count='1' />
        <TodayTestBox svg={(<img src={heartRate} alt="heartRate" />)} title='心率' count='1' />


        <TodayTestBox svg={(<img src={heartOxygen} alt="heartOxygen" />)} title='血氧' count='1' />
        <TodayTestBox svg={(<img src={bodyFat} alt="bodyFat" />)} title='体脂' count='1' />
        <TodayTestBox svg={(<img src={temperature} alt="temperature" />)} title='体温' count='1' />
      </div>
    </React.Fragment>
  );
}